
.header-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;

}

.header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-content button {
    color: white;
    border: 2px solid rgb(67, 136, 180);
    margin: 5px 0px;
    padding: 0px 5px;
    border-radius: 4px;
}

.header-content button:hover {
    background-color:rgb(67, 136, 180) ;
    color: white;
    font-weight: 500;
    transition: background-color ease-in-out 300ms;
}

.footer-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 50px;
    background-color: rgba(0, 0, 0, 0.914);
    color: white;
   
}

.exc-one-conteiner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.exc-one-conteiner-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.exc-one-conteiner-button button{
    margin: 5px 5px;
    border: 2px solid red;
    padding: 2px 2px;
}


.exc-two-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
    background-image: url("https://www.scottishlandscapepainting.co.uk/wp-content/uploads/2015/01/Aberdeen-painting-print-beach-Scotland-art-canvas-scaled.jpg");
    background-size: cover;
}
.exc-two-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 200px;
    width: 250px;
    background-color: rgb(246, 246, 246) ;
    color: red;
}

.exc-two-card p {
    font-size:xx-large;
}

.btn-exc-two button {
    margin: 4px 4px;
    padding: 4px 4px;
    border-radius: 5px;
}

#btn-start {
    background-color: rgb(24, 179, 24);
    color: white;
}

#btn-pause {
    background-color: rgb(210, 14, 14);
    color: white;
}

#btn-reset {
    background-color: rgb(3, 1, 13);
    color: rgb(246, 244, 244);
}

#btn-resume {
    background-color: rgb(3, 1, 13);
    color: rgb(246, 244, 244);
}

.exc-three-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
    background-color: burlywood;
}

.exc-three-content {
    display: flex;
    flex-direction: row;
    margin : 5px 0px;
    
}

.exc-three-content input{
    border: 1px solid black;
    margin: 0px 5px;
}


